import React, { Component } from 'react'
import { Button } from 'react-bootstrap';
import Typewriter from 'typewriter-effect';


export default class Start extends Component {

    state =
        {
            width: 0,
            height: typeof window !== 'undefined' ? window.innerHeight : 1000,
            countDownDate: new Date("Oct 9, 2020 12:00:00").getTime(),
            result: "0d 0h 0m 0s"
        };

    constructor(props) {
        super(props);
        this.updateWindowDimensions = this.updateWindowDimensions.bind(this);

    }

    componentDidMount() {
        this.updateWindowDimensions();
        window.addEventListener('resize', this.updateWindowDimensions);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateWindowDimensions);
    }

    updateWindowDimensions() {
        this.setState({ width: window.innerWidth, height: window.innerHeight });
    }



    render() {
        return (
            <div>
                <div id="start" className="bg-image" style={{
                    height: this.state.height + 150,
                    backgroundImage: "url(images/bg.jpg)",
                }}></div>
                <div className="bg-text">
                    <p style={{
                        // paddingTop: this.state.height / 2.5,
                        color: "white",
                        textAlign: "center"
                    }}>
                        Hey! Welcome to my mostly-finished portfolio site.
                </p>
                    <h1>I'm khalid</h1>


                    <div className="typeWriter">

                        <span >I'm a </span>
                        <Typewriter
                            wrapperClassName="typeWriter"
                            options={{
                                strings: ["FullStack Developer", "Freelancer", "Web Developer"],
                                autoStart: true,
                                loop: true,
                            }}
                        />
                        <a href="#about">
                            <Button
                                className="actionBTN button"
                            >
                                About
                    </Button>
                        </a>
                    </div>
                </div>


                {/*

                */}
            </div>

        )
    }
}
