import React, { Component } from 'react'
import { Navbar, Nav } from 'react-bootstrap';

export default class Header extends Component {

    state = {
        navBackground: "none",
        headerStyle: "headerStyleBefore",
        icon: "images/first.png",
        width: "50",
        height: "50"

    }
    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    handleScroll = (event) => {
        if (window.scrollY > 2)
            this.setState({
                navBackground: "light",
                headerStyle: "headerStyleAfter",
                icon: "images/second.png",
                width: "80",
                height: "80"
            });
        else
            this.setState({
                navBackground: "none",
                headerStyle: "headerStyleBefore",
                icon: "images/first.png",
                width: "50",
                height: "50"

            });

    }


    render() {

        return (

            <Navbar id="home" sticky="top" expand="md"
                className="marginHeader transBg" bg={this.state.navBackground}  >
                <Navbar.Brand href="#home">
                    <img
                        alt=""
                        src={this.state.icon}
                        width={this.state.width}
                        height={this.state.height}
                        className="d-inline-block align-top transIcon"
                    />
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className={this.state.headerStyle + " ml-auto"} >
                        <Nav.Link href="#about">About</Nav.Link>
                        <Nav.Link href="#projects">Projects</Nav.Link>
                        <Nav.Link href="#contact">Contact</Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Navbar >

        )
    }
}
