import React, { Component } from 'react'
import { Button } from 'react-bootstrap'

export default class About extends Component {
    render() {
        return (
            <div className="bgImg">

                <div id="about" className="aboutStyle container">
                    <div className="row">
                        <div className="col-sm-12 col-md-6">

                            <h3 className="text-orange title.is-3 title">
                                About me
                        </h3>

                            <h4 style={{
                                color: "#999",
                                fontSize: "1.2rem",
                                fontWeight: "500",
                                marginBottom: ".5rem",
                            }} >
                                Full Stack Developer
                        </h4>
                            <span className="bottomLine"></span>
                            <p>
                                Junior Developer, with a strong background
                                in solving problems and programming. I
                                have a great attention to details and
                                significant ability to work in team
                                environments
                        </p>
                            <p>
                                I love finding practical solutions and collaborating with quality people to build web sites and web applications.
                        </p>
                            <p>
                                If you're interested, I invite you to learn more about <a style={{ color: "#f30" }} href="#tools">the tools</a> I use to build websites or the services I offer.
                            </p>
                            <a href="#contact">
                                <Button className="button actionBTN btnHover">
                                    Hire me
                            </Button>
                            </a>
                        </div>

                        <div className="col-sm-12 col-md-6 text-center">

                            <img
                                alt="khalid moussaid profil"
                                className="img-fluid img-thumbnail imgStyle"
                                src="images/profil.jpg" />
                        </div>
                    </div>

                </div >
            </div>

        )
    }
}
