import React, { Component } from 'react'
import { Button } from 'react-bootstrap'

export default class Contact extends Component {

    state = {
        name: "",
        message: "",
        email: "",
        email_err: ""
    }
    SendData() {
        if (/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(this.state.email)) {
            this.setState({
                email_err: ""
            })
            const requestOptions = {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ title: 'React Hooks POST Request Example' })
            };
            fetch('http://kmoussaid.com/mail.php', requestOptions)
                .then(response => console.log(response))
        }
        else
            this.setState({
                email_err: "invalid email"
            })





    }

    render() {
        return (
            <div id="contact" className="bgImg">

                <div className="aboutStyle container">
                    <div className="row">
                        <div className="col-sm-12 col-md-6">

                            <h3 className="text-orange title.is-3 title">
                                Say Hi
                        </h3>


                            <span className="bottomLine"></span>
                            <p>
                                Want to work together? Please fill out this form.
                        </p>

                            {/* <Button className="button actionBTN btnHover">
                                <a href="#contact">
                                    Hire me
                                </a>
                            </Button> */}
                        </div>

                        <div className="col-sm-12 col-md-6">
                            <div className="form-group">
                                <label htmlFor="name">Name:</label>
                                <input
                                    value={this.state.name}
                                    type="text"
                                    onChange={(e) => this.setState({ name: e.target.value })}
                                    className="form-control"
                                    id="name"
                                    placeholder="khalid moussaid" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="email">E-mail:</label>
                                <input
                                    style={
                                        this.state.email_err.length > 0 ? {
                                            border: "solid 1px red"
                                        } : {}
                                    }
                                    onChange={(e) => this.setState({ email: e.target.value })}
                                    value={this.state.email}
                                    type="email" className="form-control" id="email" placeholder="contact@kmoussaid.com" />
                                <label
                                    style={{
                                        display: this.state.email_err.length > 0 ? "block" : "none"
                                    }}
                                    className="errLabel"
                                    htmlFor="email">{this.state.email_err}</label>
                            </div>
                            <div className="form-group">
                                <label htmlFor="message">Message:</label>
                                <textarea
                                    onChange={(e) => this.setState({ message: e.target.value })}
                                    value={this.state.message}
                                    rows="5" type="text" className="form-control" id="message" placeholder="Message" />
                            </div>
                            <a href="#contact">
                                <Button
                                    onClick={() => this.SendData()}
                                    className="button actionBTN">

                                    Send
                            </Button>
                            </a>


                        </div>
                    </div>

                </div >
            </div >
        )
    }
}
