import React, { Component } from 'react'
import { Modal, Button, Carousel } from 'react-bootstrap'


function MyVerticallyCenteredModal(props) {
    const { modalProject, ...rest } = props

    return (
        <Modal
            {...rest}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title id="contained-modal-title-vcenter">
                    {modalProject.name}
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Carousel
                    interval={1000}
                    indicators={false}
                >
                    {modalProject.images.map((img, index) => {
                        return (
                            <Carousel.Item
                                style={{
                                    height: "40vh",
                                }}
                                key={index}
                                interval={500}>
                                <img
                                    className="d-block w-100"
                                    src={img}
                                    alt={modalProject.name}
                                />
                            </Carousel.Item>
                        )
                    })}
                </Carousel>
                <p>
                    {modalProject.desc}
                </p>
                <h4>Technologies used</h4>
                <span className="bottomLine"></span>
                <div className="row justify-content-center">
                    {
                        modalProject.techs.map((tech, index) => {
                            return (

                                <div
                                    key={index}
                                    className="col-3"
                                    style={{
                                        textAlign: "center"
                                    }}>

                                    <img
                                        alt={tech.name}
                                        height="70px"
                                        src={tech.iconSrc} />
                                    <br />
                                    <span>{tech.name}</span>
                                </div>
                            )
                        })
                    }

                </div>


            </Modal.Body>
            <Modal.Footer>
                <Button className="actionBTN" onClick={props.onHide}>Close</Button>
            </Modal.Footer>
        </Modal>
    );
}

export default class Project extends Component {

    state = {
        modalShow: false,
        setModalShow: false,
        modalProject: {
            images: [],
            techs: []
        },
        project: [
            {
                name: "Math On Demand",
                desc: "Math on demand is a platform that allows students to ask complicated math questions and get answers from professors based on the tag specified of the question.",
                images: ["images/mod/mod_1.png", "images/mod/mod_2.png", "images/mod/mod_3.png"],
                techs: [
                    {
                        name: "NodeJs",
                        iconSrc: "images/tech/nodejs.png",
                    },
                    {
                        name: "ReactJs",
                        iconSrc: "images/tech/reactjs.png",
                    },
                    {
                        name: "MongoDB",
                        iconSrc: "images/tech/mongodb.png",
                    },
                    {
                        name: "Material-UI",
                        iconSrc: "images/tech/materialUi.png",
                    },
                ]
            }
        ]
    }

    openModel = (index) => {
        this.setState(
            {

                modalShow: true,
                modalProject: this.state.project[index]
            })
    }
    render() {
        return (
            <div id="projects" className="container-fluid" style={{
                textAlign: "center",
                padding: "25px",
                backgroundColor: "white"
            }}>

                <h3 className="text-orange title.is-3 title">
                    Some Recent Projects
                        </h3>


                <span className="bottomLine"></span>
                <br />
                <br />
                <div className="row justify-content-center" style={{
                }}>
                    {this.state.project.map((elem, index) => {
                        return (
                            <div
                                role="button"
                                tabIndex="0"
                                key={index}
                                align="center"
                                className="card cardTrans col-sm-6 col-md-4 col-lg-3"
                                onClick={() => this.openModel(index)}
                                onKeyDown={() => { }}
                            >
                                <div className="containerImg card-img">
                                    <div className="filterDiv">
                                        <img src={elem.images[0]} alt={elem.name} style={{ width: "100%" }} />
                                    </div>
                                    <div className="centered">
                                        <h1 className="title">
                                            {elem.name}
                                        </h1>
                                    </div>
                                </div>
                            </div>
                        )
                    })}
                </div>
                <MyVerticallyCenteredModal
                    modalProject={this.state.modalProject}
                    show={this.state.modalShow}
                    onHide={() => this.setState({ modalShow: false })}
                />
            </div >
        )
    }
}
